.footer__copyright {
  text-align: center;
  padding-top: rem(43);
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
  z-index: 2;
  display: grid;
  place-items: center;
  @include mq("md") {
    grid-area: copyright;
    justify-self: center;
    width: 100%;
    max-width: rem(1000);
    padding-top: rem(70);
  }
}

.footer__copyright .footer__text {
  font-family: 'Zen Kaku Gothic New', sans-serif;
  font-size: rem(14);
  color: rgba($text-black, 0.7);
  margin: 0;
  line-height: 1.6;
}

