    margin-top: 3.75rem;
  }
}

/* ========== Swiper Base ========== */
.office-swiper {
  position: relative;
}

.office-swiper .swiper-slide,
.office-swiper .swiper-slide-active {
  transform-origin: bottom center;
}

.office-swiper .swiper-slide img {
  width: 100%;
  height: auto;
  aspect-ratio: 720/420;
  object-fit: cover;
}

.office-swiper .swiper-slide {
  transform: scale(0.8);
  transition: 1s;
  transform-origin: bottom center;
}

.office-swiper .swiper-slide-active {
  transform: scale(1) translateY(15px);
  max-width: 45rem;
}

/* ========== Pagination (共通) ========== */
.office-swiper .swiper-pagination {
  bottom: -3rem;
}

.office-swiper .swiper-pagination .swiper-pagination-bullet {
  border: 1px solid #333;
  background: #fff9ee;
}

/* ========== Controls（ページネーション＋ナビ横並び） ========== */
.office-swiper__controls {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center; /* 右寄せにする場合は flex-end */
  align-items: center;
  gap: 1.5rem;
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .office-swiper__controls {
    margin-top: 2.5rem;
  }
}

/* Swiper既定のabsoluteを解除して横並びに参加させる */
.office-swiper__controls .swiper-pagination {
  position: static !important;
  bottom: auto !important;
  left: auto !important;
  right: auto !important;
  width: auto !important;
  margin: 0 !important;
  transform: none !important;
  text-align: left; /* 好みで */
}

/* fraction体裁（01 — 06など） */
.office-swiper__controls .swiper-pagination.swiper-pagination-fraction {
  display: inline-flex !important;
  align-items: center !important;
  gap: 0.75rem !important;
  width: auto !important;
  font-feature-settings: "tnum";
}

/* JSのrenderFractionで差し込んだ区切りの余白 */
.fraction-separator {
  display: inline-block;
  margin: 0 0.75rem;
}

/* ========== Navigation Buttons ========== */
.office-swiper__nav {
  display: flex;
  gap: 1rem;
}

.office-swiper__nav .nav {
  position: static; /* 絶対配置は使わない */
  transform: none;
  width: auto;
  height: auto;
  background: transparent;
  cursor: pointer;
}

.office-swiper__nav .nav svg {
  width: 51px;
  height: 50px;
  filter: drop-shadow(0 10px 30px rgba(0, 0, 0, 0.08));
}

