@use "global" as *;


.worries {
  padding-top: rem(80);
  padding-bottom: rem(80);
  @include mq("md") {
    padding-top: rem(130);
    padding-bottom: rem(130);
  }
}

.worries__inner {
  max-width: rem(600);
  width: 100%;
  margin: 0 auto;
  padding: 0 rem(20);
  @include mq("md") {
    padding: 0 rem(25);
    max-width: rem(1130);
  }
}

.worries__items {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include mq("md") {
    margin-top: rem(60);
    flex-direction: row;
    gap: rem(40);
  }
}

.worries__items {
  margin-top: rem(40);
  display: grid;
  grid-template-columns: 1fr; /* SP: 1列 */
  gap: rem(20);
  justify-items: center;
  @include mq("md") {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: rem(22);
    row-gap: rem(40);
    grid-template-columns: 1fr 1fr;
  }
}

.worries--psychological .worries__items,
.worries--hypnosis-script .worries__items {
  @include mq("md") {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: rem(22);
    row-gap: rem(40);
  }
}
.worries--psychological .worries__items > * ,
.worries--hypnosis-script .worries__items > *  {
  @include mq("md") {
    flex: 0 1 calc((100% - (rem(22) * 2)) / 3); /* 3列ベース */
    max-width: calc((100% - (rem(22) * 2)) / 3);
  }
}

.worries--trauma .card02 {

  @include mq("md") {
    max-width: rem(520);
  }
}

.worries__text {
  font-size: rem(16);
  color: $text-black;
  line-height: 1.6;
  margin-top: rem(60);
  text-align: center;
  @include mq("md") {
    margin-top: rem(80);
  }
}

.worries--hypnosis-script .card02__title {
  text-align: center;
}


