@use "global" as *;

.office {
  padding: rem(80) 0;
  position: relative;
  @include mq(md) {
    padding: rem(130) 0;
  }
}

.office::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: calc(50% - 50vw);
  right: rem(20);
  background-image: url(../images/common/blog-list_bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: -1;
  pointer-events: none;
  @include mq(md) {
    transform: translateX(-30px);

  }
}




